import style from "./InfoCart.module.scss";
import AppContext from "../../context";
import {useContext} from "react";

const InfoCart = ({title, description, image, setIsOrderComplete}) => {
    const {onCloseCart} = useContext(AppContext);
    const clearCart = () => {
        setTimeout(() => {
            setIsOrderComplete(false)
        }, 500)
    }

    return(
        <div className={style.infoBlock}>
            <h3 className={style.title}>Корзина</h3>
            <div className={style.content}>
                <img src={image} alt="empty-cart"/>
                <h3>{title}</h3>
                <p>{description}</p>
                <button className={style.comeBack} onClick={() => {onCloseCart(); clearCart()}}>
                    <i className="fas fa-arrow-left"></i>
                    Вернуться назад
                </button>
            </div>
        </div>
    )
}

export default InfoCart
